import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmationDialogComponent } from 'app/@theme/components/confirmation-dialog/confirmation-dialog.component';
import { PlaceOrder } from 'app/@theme/model/order';
import { CommonService } from 'app/@theme/services/common.service';
import { OrderService } from 'app/@theme/services/order.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'ngx-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {

  cartList: PlaceOrder[] = [];
  isAdmin: boolean = false;
  confirmDialogMsg:string = '';

  constructor(
    private orderService: OrderService,
    private commonService: CommonService,
    private router: Router
  ) {
    this.commonService.setTitle("My Cart")
  }

  ngOnInit(): void {
      this.isAdmin = false;
      this.getOrderListByPartyId();
  }

  getAllOrderList() {
    this.orderService.getAllOrders().subscribe(
      (data) => {
        if (data["success"]) this.cartList = data["data"];
      },
      (error) => {}
    );
  }

  getOrderListByPartyId() {
    let partyId = this.commonService.getUser().userId;
    this.orderService.getOrderListByPartyIdForCart(partyId).subscribe(
      (data) => {
        if (data["success"]) this.cartList = data["data"];
      },
      (error) => {}
    );
  }

  editOrderFromCart(id){
    this.router.navigate(['pages/order/edit',id])
  }

  incrementQty(i){
    this.cartList[i].quantity++;
  }

  decrementQty(i){
    if(this.cartList[i].quantity!=1)
    this.cartList[i].quantity--;
  }

  placeOrderFromCart(){
    this.cartList.forEach(e=>{
      e.orderStatus = 'Confirmed';
    });
    this.orderService.saveFromCart(this.cartList).subscribe(
      data=>{
        if(data['success']){
          //this.toastr.success('Order placed successfully')
          this.cartList = [];
          this.getOrderListByPartyId();
        }
        //else
        //this.toastr.error(data['msg'])
      },
      error=>{
        //this.toastr.error('Internal server error')
      }
    )
  }

  deleteFromCart(id){
    this.orderService.deleteOrder(id).subscribe(
      data=>{
        if(data['success']){
          //this.toastr.success('Order deleted successfully')
          this.getOrderListByPartyId();
        }
        //else
        //this.toastr.error(data['msg'])
      },
      error=>{
        //this.toastr.error('Internal server error')
      }
    )
  }
}
